.selectric-wrapper {
  position: relative;
  cursor: pointer; }

.selectric-responsive {
  width: 100%; }

.selectric {
  border: 1px solid #E8E8E8;
  background: #FFFFFF;
  position: relative;
  border-radius: 5px;
  transition: all .25s;
  .label {
    display: block;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin: 0 30px 0 15px;
    font-size: 14px;
    line-height: 39px;
    color: #40403e;
    height: 39px;
    transition: all .25s; }
  .button {
    background: transparent;
    border-radius: 0;
    display: block;
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    width: 30px;
    height: 30px;
    color: #005895;
    text-align: center;
    font: 0/0 a;
    *font: 30px/30px Lucida Sans Unicode, Arial Unicode MS, Arial;
    &:after {
      content: " ";
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      margin: auto;
      background: url(../img/other/step-b.svg) no-repeat center center; } } }

.selectric-focus .selectric {
  border-color: #AAA; }

.selectric-hover .label {
  color: #005895; }

.selectric-hover .selectric {
  border-color: #005895;
  .button {
    color: #cfcfcf;
    &:after {
      border-top-color: #cfcfcf; } } }

.selectric-open {
  z-index: 9999;
  .selectric {
    border-color: #005895;
    background: #fff; }
  .selectric-items {
    display: block; } }

.selectric-disabled {
  filter: alpha(opacity = 50);
  opacity: 0.5;
  cursor: default;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }

.selectric-hide-select {
  position: relative;
  overflow: hidden;
  width: 0;
  height: 0;
  select {
    position: absolute;
    left: -100%; }
  &.selectric-is-native {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 10;
    select {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      height: 100%;
      width: 100%;
      border: none;
      z-index: 1;
      box-sizing: border-box;
      opacity: 0; } } }

.selectric-input {
  position: absolute !important;
  top: 0 !important;
  left: 0 !important;
  overflow: hidden !important;
  clip: rect(0, 0, 0, 0) !important;
  margin: 0 !important;
  padding: 0 !important;
  width: 1px !important;
  height: 1px !important;
  outline: none !important;
  border: none !important;
  *font: 0/0 a !important;
  background: none !important; }

.selectric-temp-show {
  position: absolute !important;
  visibility: hidden !important;
  display: block !important; }

/* Items box */

.selectric-items {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  background: #F8F8F8;
  border: 1px solid #C4C4C4;
  z-index: -1;
  box-shadow: 0 0 10px -6px;
  .selectric-scroll {
    height: 100%;
    overflow: auto; } }

.selectric-above .selectric-items {
  top: auto;
  bottom: 100%; }

.selectric-items {
  border-radius: 0 0 5px 5px;
  ul {
    list-style: none;
    padding: 0;
    margin: 0;
    font-size: 14px;
    line-height: 20px;
    min-height: 20px; }
  li {
    list-style: none;
    margin: 0;
    font-size: 14px;
    line-height: 20px;
    min-height: 20px;
    display: block;
    padding: 8px 15px;
    border-top: 1px solid #FFF;
    border-bottom: 1px solid #EEE;
    color: #666;
    cursor: pointer;
    transition: all .25s;
    &.selected {
      background: #EFEFEF;
      color: #40403e; }
    &.highlighted {
      background: #005895;
      color: #fff; }
    &:hover {
      background: #d4d4d4;
      color: #40403e; } }
  .disabled {
    filter: alpha(opacity = 50);
    opacity: 0.5;
    cursor: default !important;
    background: none !important;
    color: #666 !important;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none; }
  .selectric-group {
    .selectric-group-label {
      font-weight: bold;
      padding-left: 10px;
      cursor: default;
      -webkit-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      user-select: none;
      background: none;
      color: #444; }
    &.disabled li {
      filter: alpha(opacity = 100);
      opacity: 1; }
    li {
      padding-left: 25px; } } }
