.screen {
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
	padding: 50px 0 55px;
	@include md {
		padding: 30px 0 45px; }
	&__container {
		@include sm {
			display: table; } }
	&__col {
		position: relative;
		&:nth-child(1) {
			width: 23%;
			float: left;
			@include lg {
				width: 26%; } }
		&:nth-child(2) {
			float: right;
			width: 74.32433%;
			@include lg {
				width: 72%; } }
		&:nth-child(3) {
			float: right;
			width: 74.32433%; } }
	&__sld-wrapp {
		@include md {
			width: 100%; } }
	&__sld {
		max-width: 825px;
		margin: 20px auto 0;
		overflow: hidden;
		border-radius: 5px;
		@include sm {
			margin: 0 -15px;
			border-radius: 0; } }
	&__sld-item {
		position: relative; }
	&__sld-pic {
		img {
			width: 100%; } }
	&__sld-inner {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		left: 60px;
		z-index: 2;
		font-size: 0;
		@include lg {
			left: 20px; } }
	&__sld-text {
		display: inline-block;
		vertical-align: middle;
		max-width: 530px;
		cursor: default; }
	&__sld-title {
		font-size: 30px;
		line-height: (30/25);
		color: #005895;
		font-family: $PEB;
		@include sm {
			font-size: 24px; } }
	&__sld-descr {
		margin: 5px 0 15px;
		line-height: (40/25);
		font-family: $PL;
		color: #40403e;
		font-size: 25px;
		@include sm {
			font-size: 19px; } }
	&__sld-btn {
		height: 50px;
		line-height: 50px;
		padding: 0 37px;
		font-family: $PS;
		font-size: 15px;
		color: #000;
		@include sm {
			padding: 0 5px; }
		&:hover .icon {
			fill: $orange; }
		.icon {
			vertical-align: initial;
			margin-left: 3px;
			font-size: 12px;
			fill: #40403e;
			@include tr(fill .25s);
			@include sm {
				display: none; } } }
	&__sld-arrow {
		position: absolute;
		top: calc(50% - 10px);
		z-index: 3;
		background-color: transparent;
		font-size: 0;
		.icon {
			font-size: 20px; }
		&:hover .icon {
			fill: #005895; }
		&--prev {
			@include rotate(180);
			left: 20px;
			@include lg {
				left: 5px; } }
		&--next {
			right: 20px;
			@include lg {
				right: 5px; } } }

	&--parts {
		padding: 50px 0 40px;
		@include md {
			padding: 20px 0 30px; }
		@include sm {
			padding-bottom: 0; }
		.screen__col:nth-child(1) {
			@include md {
				display: none; } }
		.screen__col:nth-child(2) {
			@include md {
				width: 100%; } } }
	&--contacts {
		padding: 50px 0;
		@include sm {
			padding: 20px 0 40px; }
		.screen__col:nth-child(1) {
			@include lg {
				display: none; } }
		.screen__col:nth-child(2) {
			@include lg {
				width: 100%; } } }
	&--personal {
		padding: 50px 0 90px;
		@include md {
			padding: 20px 0 30px; }
		.screen__col:nth-child(1) {
			@include md {
				display: none; } }
		.screen__col:nth-child(2) {
			@include md {
				width: 100%; } } }
	&--foreign {
		padding: 50px 0 20px;
		.screen__col:nth-child(1) {
			@include md {
				display: none; } }
		.screen__col:nth-child(2) {
			@include md {
				width: 100%; } } }
	&--basket {
		padding: 50px 0 85px;
		@include md {
			padding: 20px 0 30px; }
		.screen__col:nth-child(1) {
			@include md {
				display: none; } }
		.screen__col:nth-child(2) {
			@include md {
				width: 100%; } } }
	&--search {
		@include md {
			padding: 20px 0 45px; }
		.screen__col:nth-child(1) {
			@include md {
				display: none; } }
		.screen__col:nth-child(2) {
			@include md {
				width: 100%; } } }
	&--home {
		.screen__col:nth-child(1) {
			@include md {
				width: 35%; }
			@include sm {
				float: none;
				width: 100%;
				display: table-footer-group; } }
		.screen__col:nth-child(2) {
			@include md {
				width: 63.5%; }
			@include sm {
				float: none;
				display: table-header-group;
				width: 100%; } }
		.screen__col:nth-child(3) {
			@include lg {
				width: 72%; }
			@include md {
				width: 100%; }
			@include sm {
				display: none; } }
		.search__title {
			@include sm {
				display: none; } } } }

