// stepper plugin
.stepper {
	margin: 0 auto;
	width: 61px;
	height: 41px;
	border-radius: 5px;
	border: 1px solid #e8e8e8;
	background-color: #ffffff;
	overflow: hidden;
	position: relative;
	.stepper-input {
		padding: 0 22px 0 10px;
		background: #fff;
		border-radius: 3px;
		color: #8a8a89;
		line-height: 41px;
		margin: 0;
		overflow: hidden;
		width: 100%;
		height: 100%;
		z-index: 49;
		-moz-appearance: textfield;
		font-family: $PR;
		font-size: 14px;
		&::-webkit-inner-spin-button, &::-webkit-outer-spin-button {
			-webkit-appearance: none;
			margin: 0; }
		&:focus {
			background-color: #fff; } }
	.stepper-arrow {
		border-left: 1px solid #e7e7e7;
		cursor: pointer;
		display: block;
		height: 50%;
		position: absolute;
		right: 0;
		text-indent: -99999px;
		width: 20px;
		z-index: 50;
		@include tr(all .2s);
		&:hover {
			background-color: #E7E7E6; }
		&.up {
			top: 0;
			&:active::after {
				transform: translateY(-3px); }
			&::after {
				height: 5px;
				width: 8px;
				content: '';
				position: absolute;
				left: 6px;
				display: block;
				top: 11px;
				background: url('../img/other/step-t.svg') no-repeat center center;
				@include tr(all .2s); } }
		&.down {
			bottom: 0;
			&::after {
				height: 5px;
				width: 8px;
				content: '';
				position: absolute;
				left: 6px;
				display: block;
				top: -17px; }
			&:active::after {
				transform: translateY(3px); }
			&::after {
				top: 5px;
				background: url('../img/other/step-b.svg');
				@include tr(all .2s); } } }
	&.disabled {
		.stepper-input {
			background: #fff;
			border-color: #eee;
			color: #ccc; }
		.stepper-arrow {
			background: #fff;
			border-color: #eee;
			cursor: default; } } }
