// discount block
.disblock {
	padding: 45px 0;
	text-align: center;
	&__descr {
		font-size: 18px;
		margin-top: 5px;
		margin-bottom: 16px; }
	&__form-wrapper {
		font-size: 0; }
	&__form-field {
		width: 217px;
		display: inline-block;
		vertical-align: middle;
		&:first-child {
			margin-right: 10px;
			@include sm {
				display: block;
				margin: 0 auto 10px; } } }
	&__form-input {
		width: 100%;
		font-size: 14px;
		height: 41px;
		line-height: 41px;
		font-family: $PR;
		padding: 0 18px;
		background-color: #fff;
		border: 1px solid #e7e7e7;
		border-radius: 5px;
		color: #000;
		@include placeholder-color($gray); }
	&__form-btn {
		width: 100%;
		padding: 0 15px;
		box-shadow: 0 2px 3px rgba(14, 30, 46, 0.2);
		&:hover {
			box-shadow: none; } } }

// BREADCRUMBS
.breadcrumbs {
	padding-bottom: 5px;
	border-bottom: 1px solid #e7e7e7;
	&__list {
		font-size: 0; }
	&__item {
		position: relative;
		display: inline-block;
		vertical-align: top;
		margin: 0 19px 10px 0;
		&::after {
			display: inline-block;
			position: absolute;
			z-index: 1;
			right: -10px;
			width: 1px;
			height: 11px;
			background-color: #e7e7e7;
			content: '';
			cursor: default;
			pointer-events: none; }
		&:last-child::after {
			display: none; }
		&.is-current .breadcrumbs__link {
			border-bottom-color: transparent;
			color: #8a8a89;
			pointer-events: none;
			cursor: default; } }
	&__link {
		font-size: 14px;
		text-transform: none;
		color: #0078bf;
		border-bottom-color: #bfddef; } }

// POPULAR LINKS
.pop-links {
	@include sm {
		display: none; }
	&__inner {
		@include clr; }
	&__title {
		margin-bottom: 20px;
		color: #000;
		font-family: $PS;
		font-size: 17px;
		line-height: (20/17); }
	&__list {
		float: left;
		width: 25%;
		padding-right: 10px;
		li {
			margin-bottom: 20px; }
		a {
			font-size: 15px;
			color: #40403e;
			text-decoration: underline #c2c2c2;
			&:hover {
				text-decoration: none; } } } }

// FEED
.feed {
	padding: 50px 0;
	&__title {
		text-align: center; }
	&__inner {
		max-width: 806px;
		margin: 15px auto 0;
		font-size: 0;
		text-align: center; }
	&__row {
		margin: 0 -10px;
		& + & {
			padding: 20px 0 40px; } }
	&__field {
		display: inline-block;
		vertical-align: top;
		width: calc(33.33333% - 20px);
		margin: 0 10px;
		@include xs {
			width: calc(100% - 20px); }
		&--full {
			width: calc(100% - 20px); } }
	&__btn {
		padding: 0 20px;
		letter-spacing: 0.05em; } }

// LIMIT
.limit {
	&__inner {
		margin-top: 30px;
		@include clr;
		@include lg {
			margin-top: 15px; } }
	&__scale {
		float: left;
		width: 200px;
		height: 44px;
		@include clr;
		@include lg {
			float: none;
			margin: 0 auto; }
		i {
			float: left;
			width: (100%/33);
			height: 100%;
			border-right: 3px solid #fff;
			background-color: #e8e8e8;
			&.is-active {
				background-color: #005896; } } }
	&__status {
		float: left;
		margin-left: 15px;
		@include lg {
			float: none;
			margin: 15px auto 0;
			text-align: center; }
		&-text {
			margin-bottom: 8px;
			font-size: 18px;
			color: #40403e;
			@include lg {
				margin-bottom: 15px; } } } }

// parts
.parts {
	&__nav {
		margin-bottom: 30px;
		font-size: 0;
		&-link {
			font-size: 15px;
			text-transform: none;
			color: #0078bf;
			border-bottom-color: #0078bf;
			& + & {
				margin-left: 20px; } } }
	&__inn {
		margin: 0 -15px;
		@include clr; }
	&__col {
		float: left;
		padding: 0 15px;
		width: 50%;
		@include sm {
			width: 100%;
			&:nth-child(2) {
				padding: 25px 15px; } } }
	&__sub-list {
		display: none; }
	&__list-title {
		font-size: 15px;
		color: #40403e;
		text-decoration: none;
		&:hover span {
			border-bottom-color: transparent; }
		span {
			border-bottom: 1px solid #c2c2c2;
			@include tr(border-bottom-color .25s); }
		&.is-active .parts__list-btn::after {
			display: none; } }
	&__list {
		li {
			padding-bottom: 9px; } }
	&__list-btn {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		margin-right: 4px;
		height: 26px;
		width: 26px;
		border-radius: 5px;
		border: 1px solid #e8e8e8;
		background-color: #f5f5f6;
		color: #3f3f3d;
		font-style: normal;
		font-size: 14px;
		text-align: center;
		&::before {
			position: absolute;
			left: 5px;
			top: 11px;
			z-index: 1;
			width: 15px;
			height: 2px;
			background-color: #000;
			content: ''; }
		&::after {
			position: absolute;
			left: 11px;
			top: 5px;
			height: 15px;
			width: 2px;
			content: '';
			background-color: #000; } }
	&__sub-list {
		padding: 10px 0 0 35px;
		a {
			color: $blue;
			text-decoration: none;
			border-bottom: 1px solid #005896;
			transition: all .2s;
			&:hover {
				border-bottom-color: transparent; } } }
	&__pop {
		li {
			padding-bottom: 20px; }
		a {
			font-size: 15px;
			color: #40403e;
			text-transform: none;
			font-family: $PR; } }
	&__pop-title {
		margin-bottom: 10px;
		font-size: 17px;
		font-family: $PS;
		color: #000; } }

// cars brand logos
.cars {
	padding: 40px 0 0;
	&__row {
		font-size: 0;
		&:first-child .cars__brand-item::after {
			position: absolute;
			top: 16px;
			left: -5px;
			z-index: 1;
			height: 65px;
			width: 1px;
			background-color: #e8e8e8;
			content: '';
			pointer-events: none; } }
	&__brand {
		display: inline-block;
		vertical-align: top;
		margin-bottom: 28px;
		font-size: 0;
		&-letter {
			font-size: 30px;
			color: #8a8a89;
			font-family: $PL;
			text-align: left; }
		&-item {
			position: relative;
			display: inline-block;
			vertical-align: top;
			margin: 0 5px;
			text-decoration: none;
			text-align: center;
			@include lg {
				margin: 0 10px; }
			&:hover .cars__brand-name {
				border-bottom-color: transparent; }
			&::before {
				position: absolute;
				top: 16px;
				right: -6px;
				z-index: 1;
				height: 65px;
				width: 1px;
				background-color: #e8e8e8;
				content: '';
				pointer-events: none;
				@include lg {
					right: -16px; } } }
		&-logo {
			height: 90px;
			width: 81px;
			font-size: 0;
			&::before {
				display: inline-block;
				vertical-align: middle;
				width: 0;
				height: 100%;
				content: ''; } }
		&-img {
			display: inline-block;
			vertical-align: middle; }
		&-name {
			display: inline-block;
			font-size: 14px;
			color: #40403e;
			line-height: (15/14);
			text-transform: none;
			border-bottom: 1px solid #cfcfce;
			@include tr(all .2s); } } }

// drop
.drop {
	padding: 25px 20px 20px 20px;
	border-radius: 5px;
	border: 1px solid #e7e7e7;
	background-color: #ffffff;
	text-align: left;
	@include lg {
		padding: 20px 10px 20px 10px; }
	@include sm {
		text-align: center; }
	&__descr {
		margin: 8px 0;
		line-height: (24/15);
		font-size: 15px; }
	&__format {
		margin: 10px 0;
		color: #8a8a89;
		font-size: 14px; }
	&__format, &__descr {
		@include lg {
			margin: 15px 0; } }
	&__loaded {
		margin-bottom: 20px;
		color: #005895;
		font-size: 14px; }
	&__status {
		margin-top: 10px;
		color: #8a8a89;
		font-size: 14px; }
	&__send-btn {
		width: 100%; } }

// timer
.call-timer {
	&__count {
		display: inline-block;
		vertical-align: middle;
		margin-left: 20px;
		font-size: 25px;
		font-family: $PEB;
		@include xs {
			margin-left: 5px; } }
	&__btn {
		@include xs {
			padding-left: 10px;
			padding-right: 10px; } } }

// payment
.payment {
	.icon-visa {
		font-size: 15px;
		.icon-mc {
			font-size: 18px; }
		.icon + .icon {
			margin-left: 9px; } } }

// burger
.burger {
	display: none;
	vertical-align: middle;
	margin-right: 20px;
	position: relative;
	padding: 0;
	width: 32px;
	height: 40px;
	font-size: 0;
	text-indent: -9999px;
	box-shadow: none;
	-webkit-tap-highlight-color: transparent;
	border: none;
	cursor: pointer;
	z-index: 10;
	transition: background 0.3s;
	backface-visibility: hidden;
	background: none;
	@include sm {
		display: inline-block; }
	&:focus {
		outline: none; }

	span {
		display: block;
		position: absolute;
		top: 18px;
		left: 2px;
		right: 2px;
		height: 2px;
		background: #000;
		transition: background 0.3s;
		&::after, &::before {
			position: absolute;
			display: block;
			left: 0;
			width: 100%;
			height: 2px;
			background-color: #000;
			content: ""; }

		&::before {
			top: -8px;
			transition: top 0.2s ease 0.2s, transform 0.2s ease 0s; }

		&::after {
			bottom: -8px;
			transition: bottom 0.2s ease 0.2s, transform 0.2s ease 0s; } }

	&.is-active {
		span {
			background: none;
			&::before {
				top: 0;
				transform: rotate(45deg);
				transition: top 0.3s ease 0s, transform 0.3s ease 0.3s; }

			&::after {
				bottom: 0;
				transform: rotate(-45deg);
				transition: bottom 0.3s ease 0s, transform 0.3s ease 0.3s; } } } }

.float-phone {
	display: none;
	position: fixed;
	right: 20px;
	bottom: 20px;
	width: 45px;
	z-index: 10;
	height: 45px;
	border-radius: 50%;
	background-color: #0079c0;
	-webkit-tap-highlight-color: transparent;
	outline: none;
	box-shadow: 0 0 5px #000;
	text-align: center;
	line-height: 45px;
	@include tr(background-color .25s);
	animation: mob-ring 1s infinite alternate linear;
	@include sm {
		display: block; }
	.icon {
		font-size: 22px; } }

.catalog-btn {
	position: relative;
	height: 49px;
	padding: 0 38px;
	line-height: 49px;
	box-shadow: 0 2px 3px rgba(14, 30, 46, 0.2);
	border-radius: 5px;
	background-color: #005895;
	font-size: 13px;
	color: #fff;
	font-family: $PS;
	text-transform: uppercase;
	@include tr(background .2s);
	.icon {
		position: absolute;
		font-size: 6px;
		top: calc(50% - 4px);
		right: 15px;
		fill: #fff; }
	&:hover {
		background-color: darken(#005895, 5); } }

.page {
	padding: 60px 0 0;
	box-shadow: 0 0 15px rgba(0, 0, 0, 0.06);
	&__bar {
		font-size: 0;
		&-col {
			display: inline-block;
			vertical-align: middle;
			&:nth-child(1) {
				width: 135px;
				margin-right: 20px; }
			&:nth-child(2) {
				width: calc(100% - 155px); } }
		&-breadcrumbs {
			padding: 25px 0 8px; } } }

.main--product {
	padding-bottom: 15px; }
