.header {
	padding: 30px 0 25px;
	@include sm {
		position: relative;
		padding: 30px 0 75px; }
	&__container {
		@include sm {
			padding-bottom: 15px;
			border-bottom: 1px solid #e7e7e7; } }
	&__col {
		&:nth-child(1) {
			float: left;
			width: (257/1110 * 100%);
			@include lg {
				width: 28%; }
			@include sm {
				width: 150px;
				height: 50px; } }
		&:nth-child(2) {
			float: right;
			width: (822/1110 * 100%);
			@include lg {
				width: 71%; }
			@include sm {
				width: 50%; } }
		&:nth-child(3) {
			float: right;
			width: (822/1110 * 100%);
			@include lg {
				float: none;
				clear: both;
				width: 100%;
				padding-top: 20px; }
			@include sm {
				padding: 0; } } }
	&__top {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding-top: 5px;
		padding-bottom: 25px;
		font-size: 0;
		white-space: nowrap;
		@include lg {
			padding-top: 20px;
			padding-left: 10px;
			white-space: normal;
			text-align: center; }
		@include sm {
			padding: 0; }
		&-col {
			position: relative;
			display: inline-block;
			vertical-align: middle;
			padding: 0 25px;
			&:first-child::after {
				display: none; }
			&:last-child {
				padding-top: 5px;
				padding-right: 0;
				margin-left: auto; }
			&:nth-child(2) {
				padding-top: 5px;
				padding-right: 0;
				padding-left: 50px; }
			&::after {
				content: '';
				position: absolute;
				top: 50%;
				left: 0;
				transform: translateY(-50%);
				background: #e7e7e7;
				width: 1px;
				height: 37px; } } }
	&__social-link {
		display: inline-block;
		vertical-align: middle;
		margin-left: 20px;
		font-size: 16px;
		.icon {
			fill: #44668f; } }
	&__city-label {
		display: inline-block;
		vertical-align: middle;
		color: #40403e;
		font-size: 15px; } }

.logo {
	display: block;
	&__img {
		display: block;
		width: 100%;
		height: 100%;
		max-width: 257px;
		max-height: 82px;
		@include sm {
			margin: 0 auto; } }
	&--footer {
		@include sm {
			max-width: 170px;
			margin: 0 auto; } } }

.contacts {
	line-height: 1;
	font-size: 0;
	&__street {
		display: inline-block;
		vertical-align: middle;
		padding-right: 20px;
		font-family: $PL;
		color: $black-light; }
	&__phone {
		display: inline-block;
		vertical-align: middle;
		text-decoration: none;
		color: $black-light;
		font-size: 23px;
		font-family: $PS; }
	&--header {
		padding: 0 30px 0 0; }
	&--header &__phone {
		padding-left: 19px; }
	&--header &__street {
		position: relative;
		font-size: 16px; }
	&--header &__street::after {
		content: '';
		position: absolute;
		top: 50%;
		right: 0;
		transform: translateY(-50%);
		background: #e7e7e7;
		width: 1px;
		height: 37px; }

	&--footer {
		margin: 15px 0;
		font-size: 16px;
		@include md {
			margin: 0;
			display: inline-block;
			vertical-align: middle; }
		@include sm {
			margin: 15px 0; } }
	&--footer &__phone {
		font-size: 16px; }
	&--footer &__street {
		padding-right: 0;
		color: #000; } }

.userbar {
	font-size: 0;
	&__item {
		display: inline-block;
		vertical-align: text-bottom;
		position: relative;
		background-color: transparent;
		& + & {
			margin-left: 26px;
			@include sm {
				margin-left: 24px; } }
		.icon {
			font-size: 22px;
			fill: #40403e;
			@include tr(fill .25s); }
		&--user {
			@include s('user'); } }
	&__count {
		color: $white;
		font-size: 11px;
		width: 16px;
		height: 16px;
		line-height: 17px;
		position: absolute;
		top: -6px;
		right: -6px;
		background-color: #0078bf;
		text-align: center;
		border-radius: 50%;
		font-family: $RL; }
	&__count--cart {
		background-color: #ed691f; } }

.menu {
	font-size: 0;
	text-align: center;
	&__list {
		@include lg {
			max-width: 900px;
			margin: 0 auto;
			@include justify; }
		@include sm {
			display: none;
			padding-top: 15px;
			text-align-last: auto;
			text-align: center;
			line-height: 1; } }
	&__item {
		display: inline-block;
		margin: 0 15px;
		@include lg {
			margin: 0; }
		@include sm {
			display: block;
			padding: 10px 0;
			&:first-of-type {
				padding-top: 20px;
				border-top: 1px solid  #e7e7e7; } }
		&:first-of-type {
			margin-left: 0; }
		&:last-of-type {
			margin-right: 0; } }
	&__link {
		font-size: 15px;
		font-family: $PR;
		color: $black-light;
		border-bottom: 1px solid #c2c2c2;
		text-decoration: none;
		@include tr(border-bottom-color .15s);
		@include sm {
			display: block;
			font-size: 16px;
			border-bottom: none; }
		&:hover {
			border-bottom-color: transparent;
			color: $blue; } } }

.login-popup {
	visibility: hidden;
	opacity: 0;
	position: absolute;
	top: 150%;
	right: 65%;
	padding: 30px;
	border: 1px solid #e7e7e7;
	background-color: #f9f9f9;
	border-radius: 4px;
	font-size: 14px;
	text-align: center;
	z-index: 100;
	box-shadow: 0 3px 5px rgba(0, 0, 0, 0.15);
	&::before, &::after {
		content: '';
		position: absolute;
		width: 0;
		height: 0;
		border-style: solid; }
	&::before {
		z-index: 2;
		top: -11px;
		right: 16px;
		border-width: 0 0 14px 14px;
		border-color: transparent transparent #f9f9f9 transparent; }
	&::after {
		border-width: 0 0 13px 14px;
		border-color: transparent transparent #e7e7e7 transparent;
		top: -13px;
		right: 15px;
		z-index: 1; }
	&.is-open {
		visibility: visible;
		opacity: 1;
		animation: modalComeIn .3s cubic-bezier(0.39, 0.58, 0.57, 1); }
	&__title {
		margin-bottom: 15px; }
	&__link {
		color: #0078bf;
		text-decoration: underline;
		font-size: 15px;
		&:hover {
			text-decoration: none; } }
	&__btn {}
	&__enter {
		margin-top: 15px; } }

// animation for modal
@keyframes modalComeIn {
	0% {
		visibility: hidden;
		opacity: 0;
		transform: scale(.8, .8); }

	65.5% {
		transform: scale(1.03, 1.03); }

	100% {
		visibility: visible;
		opacity: 1;
		transform: scale(1, 1); } }
