.footer {
	background: #f5f5f5;
	padding: 40px 0;
	@include md {
		padding: 25px 0; }
	&__row {
		font-size: 0; }
	&__col {
		float: left;
		@include sm {
			float: none; }
		&:nth-child(2) {
			margin-left: 6%;
			max-width: 450px;
			@include lg {
				margin-left: 2%; }
			@include md {
				margin-left: 9%; } }
		&:nth-child(3) {
			margin-left: 5%;
			@include md {
				margin-top: 15px;
				width: 100%;
				margin-left: 0;
				text-align: center; } } }
	&__copyright {
		font-size: 14px;
		font-family: $PR;
		color: $black-light;
		text-align: center;
		margin-top: 14px;
		line-height: 1; }
	&__menu {
		font-size: 0;
		@include sm {
			display: none; }
		ul {
			display: inline-block;
			vertical-align: top;
			&:first-of-type {
				margin-right: 60px; } } }
	&__menu-item {
		margin-top: 30px;
		&:nth-of-type(1) {
			margin-top: 0; }
		@include md {
			margin-top: 15px; } }
	&__menu-link {
		text-transform: uppercase;
		font-size: 15px;
		font-family: $PS;
		color: $black-light;
		line-height: 1;
		letter-spacing: 0.05em; }
	&__dev {
		color: #8a8a89;
		font-size: 14px;
		font-family: $PR;
		line-height: 1;
		@include md {
			margin-top: 10px;
			text-align: center; } }
	&__dev-link {
		display: inline-block;
		color: #ef7937;
		text-decoration: underline;
		padding-left: 25px;
		position: relative;
		&:hover {
			text-decoration: none; }
		&:before {
			content: '';
			@include vcenter;
			left: 10px;
			@include s(dev); } }

	&__address-link {
		display: inline-block;
		vertical-align: middle;
		padding-left: 14px;
		border-left: 1px solid #e7e7e7;
		margin-left: 15px;
		height: 20px;
		line-height: 20px;
		.link-default {
			text-transform: none;
			font-size: 15px; } }
	&__payment {
		margin-bottom: 40px;
		@include s('payment'); } }

.socials {
	font-size: 0;
	&__item {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		margin-left: 5px;
		border: 1px solid #bdbdbd;
		width: 35px;
		height: 35px;
		border-radius: 50%;
		text-align: center;
		@include tr(border-color .3s);
		&:hover {
			border-color: darken($blue, 15); }
		&:hover .icon {
			fill: #ee6919; }
		&:first-of-type {
			margin-left: 0; }
		.icon {
			@include vcenter;
			font-size: 16px;
			fill: $blue;
			@include tr(fill .3s); }
		.icon-vk {
			font-size: 13px; }
		.icon-od {
			font-size: 19px; } }
	&--footer {
		margin-bottom: 35px;
		@include lg {
			text-align: center; }
		@include md {
			display: inline-block;
			vertical-align: middle;
			margin-bottom: 0;
			margin-left: 10px; } } }

