$icons: (0:0);
$icons: map-merge($icons,('dev': (X: -151px, Y:-95px, W: 16px, H: 20px, TW: 433px, TH: 235px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('gaz': (X: -285px, Y:0px, W: 148px, H: 57px, TW: 433px, TH: 235px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('kamaz': (X: -285px, Y:-67px, W: 128px, H: 59px, TW: 433px, TH: 235px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('payment': (X: -285px, Y:-199px, W: 104px, H: 29px, TW: 433px, TH: 235px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('paz': (X: 0px, Y:-171px, W: 136px, H: 64px, TW: 433px, TH: 235px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('selhoz': (X: -141px, Y:0px, W: 134px, H: 82px, TW: 433px, TH: 235px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('spec-tech': (X: 0px, Y:0px, W: 131px, H: 85px, TW: 433px, TH: 235px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('uaz': (X: -285px, Y:-136px, W: 139px, H: 53px, TW: 433px, TH: 235px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('user': (X: -399px, Y:-199px, W: 20px, H: 23px, TW: 433px, TH: 235px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('zil': (X: 0px, Y:-95px, W: 141px, H: 66px, TW: 433px, TH: 235px, IMG: '../img/sprite.png')));


// Gets an attribute from the sass map
@function icon-attr($icon, $attr) {
  $icon: map-get($icons, $icon);
  @return map-get($icon, $attr); }

@mixin sprite($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  width: icon-attr($iconName, W);
  height: icon-attr($iconName, H);
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-position($iconName) {
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-retina($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  $width: icon-attr($iconName, W);
  $height: icon-attr($iconName, H);
  width: $width/2;
  height: $height/2;
  $x: icon-attr($iconName, X);
  $y: icon-attr($iconName, Y);
  background-position: $x/2 $y/2;
  $tw: icon-attr($iconName, TW);
  $th: icon-attr($iconName, TH);
  background-size: $tw/2 $th/2; }


@mixin s($i) {
  @include sprite($i); }
@mixin sp($i) {
  @include sprite-position($i); }
@mixin sr($i) {
  @include sprite-retina($i); }

